"use client";

export default function CartCard(props: any) {
    return (
        <div className={`card border-2 ${props.quantity > 0 ? "border-success" : "border-danger"}`}>
            <div className="card-body">
                <h5 className="card-title">blabla</h5>
                <p className="card-text">$7</p>
            </div>
        </div>
    )
}